header ul{
    list-style: none;
    display: none;
    margin:0;
    padding:0;
    background-color: #266867;
}

header nav{
    position: inherit;
    right: 0;
}

.mobileMenu{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: baseline;
        -ms-flex-align: baseline;
            align-items: baseline;
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
    position: inherit;
    top:5rem;
    right: 0;
}

/*trying to fix the menu inside mobile safari*/
.mobileMenu2{
    display: block !important;
    position: inherit !important;
    /* height: 100vh; */
    top: 5rem !important;
    right:0 !important;
    z-index: 10 !important;
}
.mobileMenu2 li{
    display: block !important;
    width: 100% !important;
    text-align: center !important;
}
/*until here*/

header li{
    font-size: 1.5rem;
    padding:1.625rem 0;    
}

.badge{
    padding: 0.25rem 0.5rem;
    border-radius:100%;
    background-color: #266867;
    margin-left: 0.5rem;
    color:white;
}    

header a{
    text-decoration: none;
    color:#f2e2d0;
    padding: 1.625rem 0.5rem;
    width: 100%;
}

header a:hover,
header a:active,
header a.active,
header li:hover{
    background-color: #1a4645;
    color:white;
}

@media screen and (min-width:920px){
    header ul{
        /*Added this for mobile safari: */
        /* display: inline-block; */
        /* Commented this for mobile safari: */
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: horizontal;
        -webkit-box-direction: normal;
            -ms-flex-direction: row;
                flex-direction: row;
                /*until here*/
        top:0;
        background-color: inherit;
    }
    header li{
        margin-left: 0.5rem;
        font-size: 1.5rem;
        padding:1.625rem 0;        
        /* Added this for mobile safari: */
        /* display: inline-block; */
    }
}
